		console.log(this.projectList);
		console.log(this.title)
	},
	methods: {
		getData(){
			var tt = this
			uni.request({
				url:'https://dongyi.sir6.cn/api/project/',
				method:'GET',
				success(res) {
					tt.projectList = res.data.data
					console.log(res);
				}
			})
		},
		
	}
